
import appConfig from './config/app.config'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import UserModelService from './service/model/UserModel.service';

export default {
components: {
Tabbar, TabbarItem
},
data() {
return {
appConfig:appConfig,
showMenu:false,
loadMain:false,
userData:{},
showTabList:[],
teacherTabList:[
],
studentTabList:[
{title:'我的活动', link:'/campaign/find/3', icon:'home'},
{title:'发现活动', link:'/campaign/find/1', icon:'search'}
]
}
},
methods: {
toggleMenu:function () {
this.showMenu = !this.showMenu;
},
setMenu:function () {
if(this.userData && this.userData.Id) {
if(this.userData.UserType == 1) {
if(this.userData.IsAxamine == 0) {
this.showTabList = [];
} else {
this.showTabList = this.teacherTabList;
}
} else {
this.showTabList = this.studentTabList;
}
}
},
logout:function () {
UserModelService.logout((data) => {
if(data.code == 200) {

window.location.href="/api/login";
}
});
},
setUserData: function () {
let userData = this.$cookie.get('userData');
this.userData = userData ? JSON.parse(userData):null;
if(this.userData) {
if(this.userData.IsActive == 1) {
this.loadMain = true;
this.setMenu();
} else {
this.showTabList =[];
this.$vux.toast.show({
text: '账号已封，请联系管理员！',
type:'warn'
});
}
} else {
window.location.href="/api/login";
}
},
goTo(index) {
this.$router.push(this.showTabList[index]['link']);
this.showMenu = false;
}
},
created: function () {
this.setUserData();
},
watch:{
$route: function (route) {
this.setUserData();
},
}
}



