
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import CampaignModelService from '../../service/model/CampaignModel.service';
import DiscountCodeModel from '../../service/model/DiscountCodeModel.service';

export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
CellFormPreview,
Popup,
XInput
},
data() {
return {
showZheHePirce : false,
payableAmountYuan :"",
taocan : '',
showPicSrc:'',
showPriceForm : false,
labelPosition: '',
checklist001: '',
commonList: [ 'China', 'Japan', 'America' ],
Currency_unit :'￥',
discountCode:'',
email:'',
username:'',
campaignIn:{
IsFreeOfCharge:0,
},
priceList:[
],
userData:{},
payIn:{
andDiscountCode:1,
payableAmount:0.00,
priceList:[]
},
}
},
methods: {
accSub :function (num1,num2){
var r1,r2,m;
try{
r1 = num1.toString().split('.')[1].length;
}catch(e){
r1 = 0;
}
try{
r2=num2.toString().split(".")[1].length;
}catch(e){
r2=0;
}
m=Math.pow(10,Math.max(r1,r2));
var n=(r1>=r2)?r1:r2;
return (Math.round(num1*m-num2*m)/m).toFixed(n);
},
showPic:function(img){

this.showPicSrc = "../../../static/images/"+img+".jpg";
this.openPriceForm();
},
doPrice: function (status) {
},
openPriceForm: function () {
this.showPriceForm = true;

},
getCampaignIn:function (campaignId) {
CampaignModelService.getParticipateCampaignIn({CampaignId:campaignId}, (data) => {
if(data.code == 200) {
this.campaignIn = data.result.campaignIn;
this.priceList = data.result.priceList;
if(this.campaignIn.IsFreeOfCharge == 0) {
this.setPriceIn();
}
} else {
this.$vux.toast.show({
text: data.message,
type:'warn'
});
}
});
},
setPriceIn:function () {
this.payIn.priceList = [{label:'原价格', value:'HK$ 4899.00'},{label:'早鸟价', value:'HK$ 3899.00'}];
this.Currency_unit = this.priceList[0].Currency_unit;
/*if (this.Currency_unit !='￥') {
this.showZheHePirce = true;
}
if(this.priceList.length > 1) {  //有早鸟价
this.payIn.payableAmount = this.priceList[1].Price;
this.payIn.priceList[0].value = this.Currency_unit+' '+this.priceList[0].Price;
this.payIn.priceList.push({label:'早鸟价', value:this.Currency_unit+' '+this.payIn.payableAmount});
this.payIn.andDiscountCode =0;
} else {
this.payIn.andDiscountCode = 0;
this.payIn.payableAmount = this.priceList[0].Price;
this.payIn.priceList[0].value = this.Currency_unit+' '+this.payIn.payableAmount;
}
this.payableAmountYuan = (Math.floor(this.payIn.payableAmount * 80) / 100).toFixed(2);*/
},
doParticipateCampaign:function () {

if ( !this.email ) {
this.$vux.toast.show({
text: '请填写真实邮箱，否则无法收到注册信息',
type:'warn'
});
return false;
}

var reg = new RegExp("^(\\w-*\\.*)+@(\\w-?)+(\\.\\w{2,})+$"); 
if ( !reg.test(this.email)  ) {
this.$vux.toast.show({
text: '请填写真实邮箱，否则无法收到注册信息',
type:'warn'
});
return false;
}




if ( !this.username ) {
this.$vux.toast.show({
text: '请填写真实姓名，届时凭有效身份证件领取门票',
type:'warn'
});
return false;
}
CampaignModelService.participateCampaign({
CampaignId:this.$route.params.campaignId,
shareFromUserId:this.$route.params.shareFromUserId,
DiscountCode:this.discountCode,
Email:this.email,
Username:this.username,
Currency_unit:'HK$',
taocan : this.taocan
},
(data) => {
if(data.code == 200) {
if(data.result.PayableAmount == 0) {
this.$vux.toast.show({
text: '参与活动成功！'
});
this.$router.push('/campaign/detail/'+this.$route.params.campaignId);
} else {    //执行支付
window.location.href='/api/pay/do?OrderNo='+data.result.OrderNo;
}
} else {
this.$vux.toast.show({
text: data.message,
type:'warn'
});
}
}
);
},
useDiscountCode:function () {
if(this.discountCode.length == 8) {
DiscountCodeModel.useDiscountCode(
{
DiscountCode: this.discountCode,
CampaignId: this.$route.params.campaignId
}, (data) => {
if (data.code == 200) {
if(this.payIn.priceList.length > 2){
this.setPriceIn();
this.payIn.priceList.slice(2, 1);
}
let payableAmount = parseFloat(this.payIn.payableAmount);
if(data.result.DiscountType == 1) { //优惠金额
this.payIn.payableAmount = this.accSub(payableAmount,parseFloat(data.result.DiscountAmount));
this.payIn.priceList.push({label:'优惠', value:'-'+ this.Currency_unit+' '+data.result.DiscountAmount});
} else if(data.result.DiscountType == 2) {  //优惠折扣
this.payIn.payableAmount = payableAmount * (parseFloat(data.result.DiscountPercent)/10);
this.payIn.priceList.push({label:'优惠', value:'-'+ this.Currency_unit+' '+(payableAmount-this.payIn.payableAmount)});
} else if(data.result.DiscountType == 3) {  //免费
this.payIn.priceList.push({label:'优惠', value:'-'+ this.Currency_unit+' '+payableAmount});
this.payIn.payableAmount = 0;
}
this.payableAmountYuan = (Math.floor(this.payIn.payableAmount * 80) / 100).toFixed(2);

} else {
this.$vux.toast.show({
text: data.message,
type: 'warn'
});
}
});
} else{
this.setPriceIn();
}
}
},
created: function () {
let userData = this.$cookie.get('userData');
this.userData = userData ? JSON.parse(userData):null;
if (this.$route.params.campaignId) {
this.getCampaignIn(this.$route.params.campaignId);
}
}
}




