import Vue from 'vue';
import appConfig from '../../config/app.config'
import ApiService from '../../service/request/Api.service';

export default {
	props: {
		setFileList: {
			type: Array,
			default: []
		},
	},
  components: {
  },
  data() {
    return {
			fileList: [],
			appConfig:appConfig
    }
  },
  methods: {
    uploadImg: function (e) {
			let target = e.target;
			let src = target.value;
			if (this.fileList.length >= 6) {
				this.$vux.toast.show({
					text: '最多上传6张！',
					type: 'warn'
				});
				return false;
			}
			if (!src && !this.isImage(src)) {
				this.$vux.toast.show({
					text: '请选择图片！',
					type: 'warn'
				});
				return;
			}
			let file = target.files[0];
			if (file.size > this.maxSize) {
				this.$vux.toast.show({
					text: '图片超出尺寸！',
					type: 'warn'
				});
				return false;
			}
			this.$vux.loading.show({
				text: '上传中..'
			});
			let formData = new FormData();
			formData.append('FileType', 1);
			formData.append('file', file);

			let url = ApiService.getRequestUrl("fileUpload");
			Vue.http.post(url, formData, {
				responseType: 'json',
				emulateJSON: true
			}).then(response => {
				this.$vux.loading.hide();
				let data = response.body;
				if (data.code == 200) {
					this.fileList.push(data.result);
					this.outPutFile();
					// alert("上传成功");
				} else {
					this.$vux.toast.show({
						text: data.message,
						type: 'warn'
					});
				}
				target.value = "";
			}, errorData => {
				this.$vux.toast.show({
					text: '上传失败！',
					type: 'warn'
				});
			});
		},
    outPutFile:function () {
			if (this.fileList.length > 0) {
				let fileId = [];
				for (let key in this.fileList) {
					fileId.push(this.fileList[key].FileId);
				}
				this.$emit('outputFile', fileId);
			}
		},
		isImage: function (src) {
			let imgReg = /\.(gif|jpg|jpeg|png|GIF|JPG|PNG)$/;
			if (imgReg.test(src)) {
				return true;
			}
			return false;
		},
		deleteFile:function (index) {
			this.fileList.splice(index, 1);
			this.outPutFile();
		}
  },
  created: function () {
  },
	watch:{
		setFileList: function (fileList) {
			this.fileList = fileList;
		},
	}
}




